import AnchorLink from "react-anchor-link-smooth-scroll";


const DotGroup = ({ selectedPage, setSelectedPage }) => {
    const selectedStyles = `relative bg-yellow before:absolute before:w-6 before:h-6
    before:rounded-full before:border-2 before:border-yellow before:left-[-50%]
    before:top-[-50%]`;

    return <div className="flex flex-col gap-6 fixed top-[60%] right-7"> {/* La position des POINTS, en bas à droite */}
    <AnchorLink //create a link that will take us to specific parts of the page
            className={`${selectedPage === 'home' ? selectedStyles : "bg-dark-grey"} 
            w-3 h-3 rounded-full`}    
            href="#home"
            onClick={() => setSelectedPage('home')}
        />

    <AnchorLink //create a link that will take us to specific parts of the page
            className={`${selectedPage === 'skills' ? selectedStyles : "bg-dark-grey"} 
            w-3 h-3 rounded-full`}    
            href="#skills"
            onClick={() => setSelectedPage('skills')}
        />

    <AnchorLink //create a link that will take us to specific parts of the page
            className={`${selectedPage === 'projects' ? selectedStyles : "bg-dark-grey"} 
            w-3 h-3 rounded-full`}    
            href="#projects"
            onClick={() => setSelectedPage('projects')}
        />

    <AnchorLink //create a link that will take us to specific parts of the page
            className={`${selectedPage === 'cv' ? selectedStyles : "bg-dark-grey"} 
            w-3 h-3 rounded-full`}    
            href="#cv"
            onClick={() => setSelectedPage('cv')}
        />

    <AnchorLink //create a link that will take us to specific parts of the page
            className={`${selectedPage === 'contact' ? selectedStyles : "bg-dark-grey"} 
            w-3 h-3 rounded-full`}    
            href="#contact"
            onClick={() => setSelectedPage('contact')}
        />
    
      
    </div>

};


export default DotGroup;